@media screen {

  
  #menuToggle {
    overflow: hidden;
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  #menuToggle input {
    display: flex;
    width: 60px;
    height: 60px;
    position: absolute;
    cursor: pointer;
    opacity: 0; /* hide input */
    z-index: 1; /* top of the menu */
  }
  
  #menuToggle span{
    width: 55px;
    height: 8px;
    margin-bottom: 10px;
    background: #7f8fa6;
    border-radius: 4px;
    transition: all .5s cubic-bezier(.08,.81,.87,.71);
  }
  
  #span1 {
    transform-origin: 4px 0px;
  }
  
  #span3 {
    transform-origin: bottom right;
  }
  
  #menuToggle input:checked ~ #span1 {
    background-color: #0c2461;
    transform: rotate(45deg) translate(8px);
  }
  #menuToggle input:checked ~ #span2 {
    background-color: #0c2461;
    transform: rotate(495deg) translate(4px);   
  }
  #menuToggle input:checked ~ #span3 {
    background-color: #0c2461;
    transform: rotate(45deg);
    opacity: 0;
  }
}
@font-face {
    font-family: "Bookman";
    src: url(/obolonsite/static/media/BOOKOS.4267d8aa.TTF) format("truetype");
}

body {
    position: relative;
    margin: 0;
}

p {
    font-family: Bookman;
    font-size: 16px;
    margin: 0;
    text-align: center;
    /* color: red; */
}

h1 {
    font-family: Bookman;
    font-size: 30px;
    font-weight: 700;
}

h2 {
    font-family: Bookman;
    font-size: 26px;
    font-weight: 700;
}



.bottles {
    display: flex;
    position: relative;
    margin: 20px 0px 40px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 320px;
    width: 90%;
}

.bottle {
    display: flex;
    position: relative;
    align-items: flex-end;
    justify-content: space-around;
    height: 320px;
    width: 80px;
    cursor: pointer;
    transition: ease-in-out 1.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.bottle :hover {
    /* height: 320px; */
    width: 100px;
}

.naming {
    display: none;
    position: absolute;
    text-align: center;
    font-weight: 900;
    font-size: 26px;
    color: white;  
    top: 50%; 
}

.bottle:hover .naming{
     display: block;  
}




  .productDescription {
    position: fixed;
    /* display: flex; */
    width: 85%;
    height: 85%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);  
    border: 1px solid #ccc;
    background: #fff;
    overflow: auto;
    border-radius: 4px;
    outline: none;
    padding: 8px;

  }
  
  .productOverlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.75);
    z-index: 103;
  }

  .ReactModal__Overlay {
    opacity: 0;
    transition: opacity 500ms ease-in-out;
  }
  
  .ReactModal__Overlay--after-open {
    opacity: 1;
  }
  
  .ReactModal__Overlay--before-close {
    opacity: 0;
  }


  /* _____________ */

  @media screen and (min-width: 480px) {
    .bottles {
      width: 100%;
    }

    .productDescription {
      height: 75%;
    }
  }

  @media screen and (min-width: 1150px) {
    .mainContainer {
      display: block;
      width: 1150px;
      margin: 0 auto;
    }
  }

  
