.wrapper {
    display: flex;
    width: 100%;
    position: fixed;
    top: 0;
    align-items: center;
    justify-content: space-between;
    height: 50px;
    background: #31582b;
    z-index: 99;
}

.headElement {
    display: flex; 
    height: 100%;
    margin: 0 15px;
    justify-content: space-around;
    align-items: center;
    cursor: pointer;
}

.menuBtn {
    display: flex;
    height: 100%;
    /* width: 50px; */
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: all 700ms ease;
}

.menuBtn:hover {
 flex-direction: row;
}

.dropDown {
    display: flex;
    height: 100%;
    justify-content: right;
    align-items: center;
    /* width: 50px; */
}

.dropDown:hover  .menu {
    visibility: visible;
    display: block;
}



.row {
    display: block;
    content: none;
    height: 8px;
    width: 8px;
    border-radius: 50%;
    background-color: white;
    margin: 2px;
}

.menu {
    visibility: hidden;
    position: absolute;
    display: none;
    align-items: center;
    justify-content: space-around;
    width: 240px;
    background: #31582b;
    /* background: linear-gradient(86deg, rgba(39,71,34,1) 49%, rgba(74,153,62,1) 84%, rgba(48,86,42,1) 99%); */
    top: 50px;
    right: 0;
    /* transform: translate(-50%, 0%);   */
}

.listMenu {
    display: flex;
    flex-direction: column;
    align-items: center;
    list-style: none;
    margin: 0;
    padding: 0;
    margin: 6px auto 30px;
}

.itemMenu {
    margin: 15px auto;
    font-size: 16px;
    font-family: "Bookman";
    color: white;
    cursor: pointer;
}

.itemMenu:active .menu  {
    /* text-decoration: underline;
    visibility: hidden;
    display: none; */
}

.menuLink {
    text-decoration: none;
    color: white;
    font-family: "Bookman";
    font-size: 18px;
    font-weight: 700;
    text-align: center;
}

.menuLink:hover {
   text-decoration: underline;
}

.itemLine {
    display: block;
    content: none;
    width: 160px;
    height: 1px;
    background-color: white;
}

.horisontMenu {
    display: none;
    position: absolute;
    width: 100%;
    top: 0;
    right: 40px;
    height: 100%;
    align-items: center;
    justify-content: flex-end;
}

.listMenuH {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    width: 90%;
    height: 100%;
    align-items: center;
    justify-content: flex-end;
    width: 70%;
}

.itemMenuH {
    color: white;
    font-size: 18px;
    font-family: "Bookman";
    margin: auto 15px;
    font-weight: 700;
    cursor: pointer;
}

.itemMenuH:hover {
      opacity: 0.7;
}


